import { Query } from 'react-apollo'
import gql from 'graphql-tag'

const ALL_USERS_QUERY = gql`
  query {
    users {
      id
      name
      email
      permissions
    }
  }
`

{
  ({ data, loading, error }) => (
    <Query query={ALL_USERS_QUERY}>
      {(data, loading, error) => (
        <div>
          <Error error={error} />
          <p>Hey</p>
        </div>
      )}
    </Query>
  )
}

export default Permissions